import React from 'react';

class StorePicker extends React.Component {
    render() {
        return (
            <form className="store-selector">
                <h2>Please Enter</h2>
                <input type="text" required placeholder="Store Name" defaultValue="getFunName()" /><button type="submit">Lessgo!</button>
            </form>
        )
    }
}
export default StorePicker;